import service from '../../service.js';
// 公用的请求地址


import uniRequest from 'uni-request';
import {
	mapState,
	mapMutations
} from 'vuex'
import mInput from '../../components/m-input.vue'
const BASE_URL = 'http://www.luominus.com/';
export default {
	components: {
		mInput
	},
	data() {
		return {
			providerList: [],
			hasProvider: false,
			account: '',
			password: '',
			positionTop: 0,
			isDevtools: false,
		}
	},
	computed: mapState(['forcedLogin']),
	methods: {
		...mapMutations(['login']),
		initProvider() {
			const filters = ['weixin', 'qq', 'sinaweibo'];
			uni.getProvider({
				service: 'oauth',
				success: (res) => {
					if (res.provider && res.provider.length) {
						for (let i = 0; i < res.provider.length; i++) {
							if (~filters.indexOf(res.provider[i])) {
								this.providerList.push({
									value: res.provider[i],
									image: '../../static/img/' + res.provider[i] + '.png'
								});
							}
						}
						this.hasProvider = true;
					}
				},
				fail: (err) => {
					console.error('获取服务供应商失败：' + JSON.stringify(err));
				}
			});
		},
		initPosition() {
			/**
			 * 使用 absolute 定位，并且设置 bottom 值进行定位。软键盘弹出时，底部会因为窗口变化而被顶上来。
			 * 反向使用 top 进行定位，可以避免此问题。
			 */
			this.positionTop = uni.getSystemInfoSync().windowHeight - 100;
		},
		bindLogin() {
			if (this.password.length ===0 &&this.account.length===0) {
				uni.showToast({
					icon: 'none',
					title: '请输入后再登录'
				});
				return;
			}
			/**
			 * 客户端对账号信息进行一些必要的校验。
			 * 实际开发中，根据业务需要进行处理，这里仅做示例。
			 */
			if (this.account.length < 5) {
				uni.showToast({
					icon: 'none',
					title: '账号最短为 5 个字符'
				});
				return;
			}
			// if (this.password.length < 6) {
			// 	uni.showToast({
			// 		icon: 'none',
			// 		title: '密码最短为 6 个字符'
			// 	});
			// 	return;
			// }
			/**
			 * 下面简单模拟下服务端的处理
			 * 检测用户账号密码是否在已注册的用户列表中
			 * 实际开发中，使用 uni.request 将账号信息发送至服务端，客户端在回调函数中获取结果信息。
			 */
			const data = {
				account: this.account,
				pwd: this.password
				// account:17766666669,
				// pwd: 1
			};
			let validUser = service.getUsers();
	
			console.log(validUser.length)
			if (validUser.length !== 0) {
				// 存在缓存数据getUsers（）对象
				// this.toMain(this.account);
			} 
			else {
				// service.toServicelogin(data)
				//需要重新登录
				var that = this;
				uniRequest.post(BASE_URL + "api/v1/User/login", data)
					.then(function(response) {
						if (response.status === 200) {
							if (response.data.code === 200){
								console.log(response);
								// 登录成功后存入缓存数据addUser 
								const newData = {
									account: data.account,
									pwd: data.pwd,
									token: response.data.data.token
								}
								service.addUser(newData)
								uni.showToast({
									icon: 'none',
									title: '登陆成功',
								});
								// that.toMain(data.account);
								uni.reLaunch({
									url: '../main/main',
								});
							}else {
							uni.showToast({
								icon: 'none',
								title: '用户账号或密码不正确',
							});
						}
						}
						else {
							uni.showToast({
								icon: 'none',
								title: '用户账号或密码不正确',
							});
						}
					}).catch(function(error) {
						console.log(error);
					});
			}
		},
		// 微信小程序等自助登录端口
		oauth(value) {
			uni.login({
				provider: value,
				success: (res) => {
					uni.getUserInfo({
						provider: value,
						success: (infoRes) => {
							/**
							 * 实际开发中，获取用户信息后，需要将信息上报至服务端。
							 * 服务端可以用 userInfo.openId 作为用户的唯一标识新增或绑定用户信息。
							 */
							this.toMain(infoRes.userInfo.nickName);
						},
						fail() {
							uni.showToast({
								icon: 'none',
								title: '登陆失败'
							});
						}
					});
				},
				fail: (err) => {
					console.error('授权登录失败：' + JSON.stringify(err));
				}
			});
		},
		getUserInfo({
			detail
		}) {
			if (detail.userInfo) {
				this.toMain(detail.userInfo.nickName);
			} else {
				uni.showToast({
					icon: 'none',
					title: '登陆失败'
				});
			}
		},
		toMain(userName) {
			this.login(userName);
			/**
			 * 强制登录时使用reLaunch方式跳转过来
			 * 返回首页也使用reLaunch方式
			 */
			if (this.forcedLogin) {
				uni.reLaunch({
					url: '../main/main',
				});
			} else {
				uni.navigateBack();
			}

		}
	},
	onReady() {
		this.initPosition();
		this.initProvider();

		this.isDevtools = uni.getSystemInfoSync().platform === 'devtools';

	}
}
